import React, { Component } from 'react';
import Gallery from '../gallery/gallery'
//import './project.scss';
import data from '../../data.json';

class Project extends Component {
  constructor() {
    super()
    this.state = {
      isGalleryOpened: false
    }
  }

  updateGalleryStatus() {
    this.setState({
      isGalleryOpened: this.state.isGalleryOpened ? false : true
    })
  }

  render() {
    return (
      <div className='education-me-wrapper'>
        <div className="sections" id='education'>
          <div className='section-description-prg-inn'>
            <div className='section-description-prg'>
              <div  className='section-description-wrap'>
                <div className='text-project-description'>{data.myProject[this.props.nameProject].info}</div>
                <div className='section-btn-wrap'>
                  <div className='btn-btn' onClick={() => this.updateGalleryStatus()} > { this.state.isGalleryOpened ? "Скрыть..." : "Раскрыть..." }  </div>
                </div>
              </div>
              {this.state.isGalleryOpened && <Gallery project={this.props.nameProject}></Gallery>}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Project;


