import React, { Component } from 'react';
//import './project.scss';
import data from '../../data.json';
import ReactBeforeSliderComponent from 'react-before-after-slider-component';
import 'react-before-after-slider-component/dist/build.css';

class ProjectBeforeAfter extends Component {
  constructor() {
    super()
    this.state = {
      isGalleryOpened: false
    }
  }

  updateGalleryStatus() {
    this.setState({
      isGalleryOpened: this.state.isGalleryOpened ? false : true
    })
  }


  render() {
    return (
      <div className='education-me-wrapper'>
        <div className="sections" id='education'>
          <div className='section-description-prg-inn'>
            <div className='section-description-prg'>
              <div className='section-description-wrap'>
                <div className='text-project-description'>{data.myProject[this.props.nameProject].info}</div>
                <div className='section-btn-wrap'>
                  <div className='btn-btn' onClick={() => this.updateGalleryStatus()} > {this.state.isGalleryOpened ? "Скрыть..." : "Раскрыть..."}  </div>
                </div>
              </div>
              {
                this.state.isGalleryOpened && data.myProject[this.props.nameProject].gallery.map((item, index) => {
                  return <div style={{display: "flex", flexDirection: "column"}}>
                    <ReactBeforeSliderComponent  key={index}
                    firstImage={{
                      imageUrl: item.before
                    }}
                    secondImage={{
                      imageUrl: item.after
                    }}
                  />
                  <div style={{height:"15px", width: "15px"}}></div>
                  </div>;
                })
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ProjectBeforeAfter;


